"use client";

import { motion } from "framer-motion";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { navItems } from "@/components/site/site-data";
import { Wand2, Command, Github, Twitter } from "lucide-react";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";

export function SiteHeader() {
  const pathname = usePathname();

  return (
    <motion.header
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <div className="rounded-3xl border border-border/40 bg-background/50 px-3 py-3 sm:rounded-full sm:px-4 backdrop-blur-xl">
        <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
          <div className="flex items-center gap-3">
            <motion.div whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
              <Badge variant="glass" className="gap-2 cursor-pointer">
                <Wand2 className="h-3 w-3" /> 
                <span className="hidden sm:inline">Gradient Forge</span>
                <span className="sm:hidden">GF</span>
              </Badge>
            </motion.div>
            <span className="hidden text-xs text-muted-foreground md:inline">
              Nitro-inspired theming studio
            </span>
          </div>
          
          <span className="text-xs text-muted-foreground md:hidden">
            Nitro-inspired theming studio
          </span>
          
          <nav className="-mx-1 flex items-center gap-1 overflow-x-auto px-1 pb-1 sm:mx-0 sm:flex-wrap sm:justify-end sm:overflow-visible sm:px-0 sm:pb-0">
            {navItems.map((item) => {
              const isActive = pathname === item.href;
              return (
                <motion.div
                  key={item.label}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <Button
                    variant={isActive ? "default" : "ghost"}
                    size="sm"
                    className={cn(
                      "shrink-0 whitespace-nowrap relative",
                      isActive && "shadow-md"
                    )}
                    asChild
                  >
                    <a href={item.href}>
                      {item.label}
                      {isActive && (
                        <motion.div
                          layoutId="activeNav"
                          className="absolute inset-0 bg-primary/10 rounded-full -z-10"
                          transition={{ type: "spring", stiffness: 380, damping: 30 }}
                        />
                      )}
                    </a>
                  </Button>
                </motion.div>
              );
            })}
            
            <div className="h-4 w-px bg-border mx-1 hidden sm:block" />
            
            {/* Command Palette Trigger */}
            <motion.div whileHover={{ scale: 1.02 }} whileTap={{ scale: 0.98 }}>
              <Button
                variant="ghost"
                size="sm"
                className="shrink-0 gap-1.5 text-muted-foreground"
                onClick={() => {
                  // Dispatch custom event to open command palette
                  window.dispatchEvent(new KeyboardEvent("keydown", { key: "k", metaKey: true }));
                }}
              >
                <Command className="h-3.5 w-3.5" />
                <span className="hidden sm:inline text-xs">Cmd K</span>
              </Button>
            </motion.div>
            
            {/* Social Links */}
            <motion.div whileHover={{ scale: 1.02 }} whileTap={{ scale: 0.98 }}>
              <Button
                variant="ghost"
                size="sm"
                className="shrink-0 px-2"
                asChild
              >
                <a href="https://github.com" target="_blank" rel="noopener noreferrer">
                  <Github className="h-4 w-4" />
                </a>
              </Button>
            </motion.div>
            
            <motion.div whileHover={{ scale: 1.02 }} whileTap={{ scale: 0.98 }}>
              <Button
                variant="ghost"
                size="sm"
                className="shrink-0 px-2"
                asChild
              >
                <a href="https://twitter.com" target="_blank" rel="noopener noreferrer">
                  <Twitter className="h-4 w-4" />
                </a>
              </Button>
            </motion.div>
          </nav>
        </div>
      </div>
    </motion.header>
  );
}
