"use client";

import { motion } from "framer-motion";
import { SiteHeader } from "@/components/site/site-header";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { MEMORY_LANE_THEME, NITRO_PUBLIC_THEMES, type ThemeId } from "@/components/theme/theme-engine";
import { useThemeContext } from "@/components/theme/theme-context";
import { useToast } from "@/components/ui/toast";
import { Image, Grid, List, Sparkles, Check, Eye } from "lucide-react";
import { useState } from "react";

const galleryThemes = [...NITRO_PUBLIC_THEMES, MEMORY_LANE_THEME];

const container = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: {
      staggerChildren: 0.05,
    },
  },
};

const item = {
  hidden: { opacity: 0, y: 20 },
  show: { opacity: 1, y: 0 },
};

export default function GalleryPage() {
  const { themeId, setThemeId, memoryLaneUnlocked } = useThemeContext();
  const { showToast } = useToast();
  const [viewMode, setViewMode] = useState<"grid" | "list">("grid");
  const [hoveredTheme, setHoveredTheme] = useState<string | null>(null);

  const handleThemeSelect = (id: ThemeId) => {
    if (id === MEMORY_LANE_THEME.id && !memoryLaneUnlocked) {
      showToast("Unlock Memory Lane by viewing all public themes!", "info");
      return;
    }
    setThemeId(id);
    showToast(`Applied ${galleryThemes.find(t => t.id === id)?.label} theme`, "success");
  };

  const isActive = (id: string) => themeId === id;
  const isLocked = (id: string) => id === MEMORY_LANE_THEME.id && !memoryLaneUnlocked;

  return (
    <main className="min-h-screen px-4 pb-20 pt-12 sm:px-6 lg:px-12">
      <section className="mx-auto flex w-full max-w-6xl flex-col gap-12">
        <SiteHeader />

        {/* Header */}
        <motion.div 
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="flex flex-col gap-3"
        >
          <Badge variant="glass" className="w-fit gap-1">
            <Image className="h-3 w-3" />
            Gallery
          </Badge>
          <div className="flex flex-wrap items-start justify-between gap-4">
            <div>
              <h1 className="text-2xl font-semibold sm:text-3xl md:text-4xl">Theme Gallery</h1>
              <p className="max-w-2xl text-sm text-muted-foreground mt-2">
                Preview every gradient preset with consistent shadcn surfaces. Use these palettes as-is or remix them
                to match your brand.
              </p>
            </div>
            <div className="flex items-center gap-2">
              <Button
                variant={viewMode === "grid" ? "default" : "ghost"}
                size="sm"
                onClick={() => setViewMode("grid")}
              >
                <Grid className="h-4 w-4" />
              </Button>
              <Button
                variant={viewMode === "list" ? "default" : "ghost"}
                size="sm"
                onClick={() => setViewMode("list")}
              >
                <List className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </motion.div>

        {/* Stats Bar */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.1, duration: 0.5 }}
          className="flex flex-wrap items-center gap-4 p-4 rounded-2xl border border-border/50 bg-background/60"
        >
          <div className="flex items-center gap-2">
            <Badge variant="glass">{NITRO_PUBLIC_THEMES.length} Public</Badge>
            <Badge variant="outline">1 Secret</Badge>
          </div>
          <div className="h-4 w-px bg-border" />
          <p className="text-sm text-muted-foreground">
            Current theme: <span className="font-semibold text-foreground">{galleryThemes.find(t => t.id === themeId)?.label}</span>
          </p>
        </motion.div>

        {/* Theme Grid */}
        <section className="space-y-4">
          <motion.div 
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.2 }}
            className="flex flex-wrap items-center justify-between gap-4"
          >
            <div>
              <h2 className="text-2xl font-semibold">All Presets</h2>
              <p className="text-sm text-muted-foreground">
                Public Nitro gradients plus the secret Memory Lane unlock.
              </p>
            </div>
            <Badge variant="glass">{galleryThemes.length} presets</Badge>
          </motion.div>

          <motion.div 
            variants={container}
            initial="hidden"
            animate="show"
            className={viewMode === "grid" 
              ? "grid gap-4 md:grid-cols-2 lg:grid-cols-3" 
              : "flex flex-col gap-3"
            }
          >
            {galleryThemes.map((theme, index) => {
              const active = isActive(theme.id);
              const locked = isLocked(theme.id);
              
              return (
                <motion.div
                  key={theme.id}
                  variants={item}
                  whileHover={!locked ? { y: -4, transition: { duration: 0.2 } } : {}}
                  whileTap={!locked ? { scale: 0.98 } : {}}
                  onHoverStart={() => setHoveredTheme(theme.id)}
                  onHoverEnd={() => setHoveredTheme(null)}
                  onClick={() => handleThemeSelect(theme.id as ThemeId)}
                  className={`
                    cursor-pointer transition-all duration-300
                    ${active ? "ring-2 ring-primary ring-offset-2 ring-offset-background" : ""}
                    ${locked ? "opacity-50 cursor-not-allowed" : ""}
                  `}
                >
                  <Card className={`
                    overflow-hidden border-border/50 bg-background/60 
                    ${active ? "shadow-lg shadow-primary/20" : "hover:shadow-lg hover:shadow-primary/5"}
                  `}>
                    <div className="relative">
                      <div 
                        className={viewMode === "grid" ? "h-28" : "h-20"} 
                        style={{ backgroundImage: theme.preview }} 
                      />
                      
                      {/* Overlay on hover */}
                      <motion.div 
                        initial={{ opacity: 0 }}
                        animate={{ opacity: hoveredTheme === theme.id && !locked ? 1 : 0 }}
                        className="absolute inset-0 bg-black/40 flex items-center justify-center"
                      >
                        <Button size="sm" variant="outline" className="bg-background/80 backdrop-blur-sm">
                          <Eye className="h-3.5 w-3.5 mr-1" />
                          Preview
                        </Button>
                      </motion.div>

                      {/* Active indicator */}
                      {active && (
                        <motion.div 
                          initial={{ scale: 0 }}
                          animate={{ scale: 1 }}
                          className="absolute top-2 right-2"
                        >
                          <Badge variant="default" className="gap-1">
                            <Check className="h-3 w-3" />
                            Active
                          </Badge>
                        </motion.div>
                      )}

                      {/* Locked indicator */}
                      {locked && (
                        <div className="absolute inset-0 bg-black/60 flex items-center justify-center">
                          <Badge variant="outline" className="gap-1">
                            <Sparkles className="h-3 w-3" />
                            Locked
                          </Badge>
                        </div>
                      )}
                    </div>
                    
                    <CardContent className={viewMode === "grid" 
                      ? "flex items-center justify-between gap-3 py-4" 
                      : "flex items-center justify-between gap-3 py-3"
                    }>
                      <div className="min-w-0">
                        <p className="text-sm font-semibold">{theme.label}</p>
                        <p className="truncate text-xs text-muted-foreground">{theme.id}</p>
                      </div>
                      {theme.id === MEMORY_LANE_THEME.id ? (
                        <Badge variant="outline" className="shrink-0">Secret</Badge>
                      ) : (
                        <Badge variant="glass" className="shrink-0">Public</Badge>
                      )}
                    </CardContent>
                  </Card>
                </motion.div>
              );
            })}
          </motion.div>
        </section>

        {/* Quick Tips */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.5 }}
          className="rounded-2xl border border-border/50 bg-background/60 p-6"
        >
          <h3 className="text-lg font-semibold mb-3">Pro Tips</h3>
          <div className="grid gap-3 sm:grid-cols-2 lg:grid-cols-3">
            <div className="flex items-start gap-2 text-sm text-muted-foreground">
              <div className="h-5 w-5 rounded-full bg-primary/10 flex items-center justify-center shrink-0 mt-0.5">
                <span className="text-xs font-semibold text-primary">1</span>
              </div>
              Click any theme to apply it instantly
            </div>
            <div className="flex items-start gap-2 text-sm text-muted-foreground">
              <div className="h-5 w-5 rounded-full bg-primary/10 flex items-center justify-center shrink-0 mt-0.5">
                <span className="text-xs font-semibold text-primary">2</span>
              </div>
              View all 20 public themes to unlock Memory Lane
            </div>
            <div className="flex items-start gap-2 text-sm text-muted-foreground">
              <div className="h-5 w-5 rounded-full bg-primary/10 flex items-center justify-center shrink-0 mt-0.5">
                <span className="text-xs font-semibold text-primary">3</span>
              </div>
              Use the Studio for detailed component preview
            </div>
          </div>
        </motion.div>
      </section>
    </main>
  );
}
